@import "../../styles/variables";

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: rgba(white, 0.96);
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.08);
  backdrop-filter: blur(10px);
  padding: 0.8rem 0;
  border-bottom: 1px solid rgba($primary-color, 0.1);

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1400px;
    margin: 0 auto;
    padding: 0 2rem;
  }

  &__brand {
    .logo-link {
      display: flex;
      align-items: center;
      text-decoration: none;
      transition: transform 0.3s ease;

      &:hover {
        transform: translateX(5px);
      }
    }

    .logo-image {
      height: 42px;
      transition: transform 0.3s ease;
    }

    .logo-text {
      margin-left: 0.8rem;
      font-size: 1.3rem;
      font-weight: 700;
      color: $primary-color;
      letter-spacing: 0.5px;
    }
  }

  &__nav {
    display: flex;
    gap: 1.8rem;

    .nav-item {
      position: relative;

      .nav-link {
        color: $text-color;
        text-decoration: none;
        font-weight: 500;
        font-size: 1.05rem;
        padding: 0.5rem 0;
        position: relative;
        transition: color 0.3s ease;

        &:hover {
          color: $primary-color;
        }
      }

      .nav-indicator {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 0;
        height: 2px;
        background: linear-gradient(90deg, $primary-color, $secondary-color);
        transition: width 0.3s ease, opacity 0.3s ease;
        opacity: 0;
      }

      &:hover .nav-indicator {
        width: 100%;
        opacity: 1;
      }
    }
  }

  &__cta {
    .cta-button {
      background: linear-gradient(45deg, $primary-color, $secondary-color);
      color: white;
      padding: 0.7rem 1.8rem;
      border-radius: 50px;
      text-decoration: none;
      font-weight: 600;
      font-size: 0.95rem;
      transition: all 0.3s ease;
      box-shadow: 0 4px 12px rgba($primary-color, 0.2);
      display: inline-block;

      &:hover {
        transform: translateY(-2px);
        box-shadow: 0 7px 20px rgba($primary-color, 0.3);
      }
    }
  }
}

@media (max-width: $breakpoint-tablet) {
  .header {
    padding: 0.6rem 0;

    &__container {
      padding: 0 1.2rem;
    }

    &__nav {
      display: none;
    }

    &__cta {
      display: none;
    }

    &__brand {
      .logo-text {
        font-size: 1.1rem;
      }
      .logo-image {
        height: 36px;
      }
    }
  }
}
