.project-details{padding:4rem 2rem;max-width:1400px;margin:0 auto;color:#2d3748}.project-details__header{display:flex;justify-content:space-between;align-items:center;margin-bottom:3rem;flex-wrap:wrap;gap:1.5rem}.project-details__header h1{font-size:2.5rem;color:#4e1861;margin:0}.project-details__content{display:grid;grid-template-columns:1fr;gap:3rem}@media(min-width: 1024px){.project-details__content{grid-template-columns:1.5fr 1fr}}.project-details__gallery{display:grid;grid-template-columns:1fr;gap:1.5rem;order:1}.project-details__gallery .gallery-item{border-radius:12px;overflow:hidden;box-shadow:0 10px 30px rgba(0,0,0,.1);transition:transform .3s ease;cursor:zoom-in}.project-details__gallery .gallery-item img{width:100%;height:auto;display:block;transition:transform .5s ease}.project-details__gallery .gallery-item:hover img{transform:scale(1.03)}@media(min-width: 1024px){.project-details__gallery{order:0}}.project-details__info{display:flex;flex-direction:column;gap:2rem}.project-details__info h2{font-size:1.8rem;color:#4e1861;margin-bottom:.5rem}.project-details__info h3{font-size:1.4rem;color:#8c2fac;margin-bottom:1rem}.project-details__info p{line-height:1.8;margin-bottom:1.5rem;font-size:1.1rem}.project-details .project-section{background:rgba(245,245,245,.5);padding:1.5rem;border-radius:10px;border-left:4px solid #4e1861}.project-details .project-section ul{padding-left:1.2rem;margin:0}.project-details .project-section li{margin-bottom:.8rem;line-height:1.6;position:relative;list-style-type:none}.project-details .project-section li::before{content:"•";color:#4e1861;font-weight:bold;display:inline-block;width:1em;margin-left:-1em}.project-details__tags .tags-container{display:flex;flex-wrap:wrap;gap:.8rem;margin-top:.5rem}.project-details__tags .tag{background:rgba(78,24,97,.1);color:#4e1861;padding:.5rem 1rem;border-radius:20px;font-size:.85rem;font-weight:500;transition:all .3s ease}.project-details__links{display:flex;flex-wrap:wrap;gap:1rem;margin-top:2rem}.project-details__links a{text-decoration:none}.project-details__links button{display:flex;align-items:center;gap:.6rem;padding:.8rem 1.5rem}.project-details__links svg{fill:currentColor}.not-found{text-align:center;padding:4rem 2rem;font-size:1.5rem;color:#2d3748}@media(max-width: 768px){.project-details{padding:2rem 1rem}.project-details__header{margin-bottom:2rem}.project-details__header h1{font-size:2rem}.project-details__content{gap:2rem}}