@import "../../styles/variables";

.home {
  padding: 0;
  max-width: 100%;
  overflow-x: hidden;

  .hero-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    min-height: 90vh;
    padding: 4rem 2rem;
    background: linear-gradient(
      135deg,
      rgba($primary-color, 0.1) 0%,
      rgba($secondary-color, 0.05) 100%
    );

    @media (min-width: $breakpoint-tablet) {
      flex-direction: row;
      padding: 4rem 6rem;
    }

    .hero-content {
      flex: 1;
      max-width: 600px;

      .greeting {
        color: $primary-color;
        margin-bottom: 1rem;
        font-size: 1.25rem;
      }

      h1 {
        font-size: 2.5rem;
        line-height: 1.2;
        margin-bottom: 1.5rem;

        span {
          color: $primary-color;
        }

        @media (min-width: $breakpoint-tablet) {
          font-size: 3.5rem;
        }
      }

      .subtitle {
        font-size: 1.25rem;
        font-weight: 400;
        color: lighten($text-color, 20%);
        margin-bottom: 2.5rem;
        line-height: 1.6;
      }

      .cta-buttons {
        display: flex;
        gap: 1rem;
      }
    }

    .hero-image {
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 3rem;

      @media (min-width: $breakpoint-tablet) {
        margin-top: 0;
        justify-content: flex-end;
      }

      .image-placeholder {
        width: 300px;
        height: 300px;
        border-radius: 50%;
        background: linear-gradient(45deg, $primary-color, $secondary-color);
        box-shadow: 0 20px 40px rgba($primary-color, 0.2);

        & img {
          width: 300px;
        }
        @media (min-width: $breakpoint-tablet) {
          width: 400px;
          height: 400px;
          & img {
            width: 400px;
          }
        }
      }
    }
  }

  .about-section {
    padding: 6rem 2rem;
    background: white;

    @media (min-width: $breakpoint-tablet) {
      padding: 8rem 6rem;
    }

    h2 {
      text-align: center;
      font-size: 2.5rem;
      margin-bottom: 3rem;
      color: $primary-color;
    }

    .about-content {
      display: flex;
      flex-direction: column;
      gap: 3rem;
      max-width: 1200px;
      margin: 0 auto;

      @media (min-width: $breakpoint-tablet) {
        flex-direction: row;
      }

      .about-text {
        flex: 2;
        font-size: 1.1rem;
        line-height: 1.8;

        p {
          margin-bottom: 1.5rem;
        }
      }

      .about-skills {
        flex: 1;

        h3 {
          margin-bottom: 1.5rem;
          font-size: 1.5rem;
        }

        ul {
          list-style: none;
          display: flex;
          flex-wrap: wrap;
          gap: 0.8rem;

          li {
            background: rgba($primary-color, 0.1);
            color: $primary-color;
            padding: 0.5rem 1rem;
            border-radius: 20px;
            font-size: 0.9rem;
            cursor: default;
            transition: all 0.2s ease;
          }
        }
      }
    }
  }

  .achievements-section {
    padding: 6rem 2rem;
    background: $light-gray;

    @media (min-width: $breakpoint-tablet) {
      padding: 8rem 6rem;
    }

    h2 {
      text-align: center;
      font-size: 2.5rem;
      margin-bottom: 3rem;
      color: $primary-color;
    }

    .achievements-grid {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
      gap: 2rem;
      max-width: 1200px;
      margin: 0 auto;

      .achievement-card {
        background: white;
        padding: 2rem;
        border-radius: 12px;
        text-align: center;
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
        transition: transform 0.3s ease;

        &:hover {
          transform: translateY(-10px);
        }

        h3 {
          font-size: 3rem;
          color: $primary-color;
          margin-bottom: 0.5rem;
        }

        p {
          color: lighten($text-color, 30%);
          font-size: 1.1rem;
        }
      }
    }
  }
}
