@import "../../styles/variables";

.projects {
  padding: 4rem 2rem;
  max-width: 1400px;
  margin: 0 auto;

  &__header {
    text-align: center;
    margin-bottom: 3rem;

    h1 {
      font-size: 2.5rem;
      color: $primary-color;
      margin-bottom: 1rem;
    }

    .subtitle {
      font-size: 1.1rem;
      color: lighten($text-color, 20%);
      max-width: 600px;
      margin: 0 auto;
    }
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
    gap: 2.5rem;
  }

  .project-card {
    border-radius: 12px;
    overflow: hidden;
    background: white;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.05);
    transition: all 0.3s ease;
    display: flex;
    flex-direction: column;
    height: 100%;

    &:hover {
      box-shadow: 0 15px 40px rgba($primary-color, 0.1);
      transform: translateY(-10px);
    }

    &__image-container {
      position: relative;
      height: 220px;
      overflow: hidden;
    }

    &__image {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: transform 0.5s ease;
    }

    &__overlay {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: linear-gradient(
        to top,
        rgba($primary-color, 0.7),
        transparent
      );
      opacity: 0;
      transition: opacity 0.3s ease;
    }

    &:hover &__overlay {
      opacity: 1;
    }

    &:hover &__image {
      transform: scale(1.05);
    }

    &__content {
      padding: 1.8rem;
      flex: 1;
      display: flex;
      flex-direction: column;

      h3 {
        font-size: 1.4rem;
        margin-bottom: 0.8rem;
        color: $text-color;
      }

      p {
        color: lighten($text-color, 20%);
        margin-bottom: 1.2rem;
        line-height: 1.6;
        flex-grow: 1;
      }
    }

    &__tags {
      display: flex;
      flex-wrap: wrap;
      gap: 0.6rem;
      margin-bottom: 1.5rem;

      .tag {
        background: rgba($primary-color, 0.1);
        color: $primary-color;
        padding: 0.4rem 0.8rem;
        border-radius: 20px;
        font-size: 0.75rem;
        font-weight: 500;
      }
    }

    &__buttons {
      display: flex;
      align-items: center;
      justify-content: space-between;

      .github-link {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background: rgba($text-color, 0.05);
        transition: all 0.3s ease;

        svg {
          width: 20px;
          height: 20px;
          fill: $text-color;
        }

        &:hover {
          background: rgba($primary-color, 0.1);
          transform: translateY(-2px);

          svg {
            fill: $primary-color;
          }
        }
      }
    }
  }
}

@media (max-width: $breakpoint-mobile) {
  .projects {
    &__header {
      margin-bottom: 2rem;

      h1 {
        font-size: 2rem;
      }
    }

    &__grid {
      grid-template-columns: 1fr;
    }

    .project-card {
      &__content {
        padding: 1.4rem;
      }
    }
  }
}
