@import "../../styles/variables";

.button {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border: none;
  border-radius: 50px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  overflow: hidden;
  font-family: inherit;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  user-select: none;
  gap: 0.6rem;

  &--primary {
    background: linear-gradient(45deg, $primary-color, $secondary-color);
    color: white;
    box-shadow: 0 4px 15px rgba($primary-color, 0.3);

    &:hover {
      box-shadow: 0 6px 20px rgba($primary-color, 0.4);
    }

    &:disabled {
      background: $light-gray;
      color: lighten($text-color, 40%);
      box-shadow: none;
      cursor: not-allowed;
    }
  }

  &--secondary {
    background: rgba($primary-color, 0.1);
    color: $primary-color;
    border: 1px solid rgba($primary-color, 0.2);

    &:hover {
      background: rgba($primary-color, 0.2);
    }

    &:disabled {
      background: $light-gray;
      color: lighten($text-color, 40%);
      border-color: $light-gray;
      cursor: not-allowed;
    }
  }

  &--outline {
    background: transparent;
    color: $primary-color;
    border: 2px solid $primary-color;

    &:hover {
      background: rgba($primary-color, 0.05);
    }

    &:disabled {
      color: lighten($text-color, 40%);
      border-color: $light-gray;
      cursor: not-allowed;
    }
  }

  &--ghost {
    background: transparent;
    color: $text-color;
    padding: 0;
    border-radius: 0;
    font-weight: 500;

    &:hover {
      color: $primary-color;
      background: transparent;
    }

    &:disabled {
      color: lighten($text-color, 40%);
      cursor: not-allowed;
    }
  }

  &--small {
    padding: 0.5rem 1.2rem;
    font-size: 0.85rem;
  }

  &--medium {
    padding: 0.8rem 1.8rem;
    font-size: 1rem;
  }

  &--large {
    padding: 1rem 2.5rem;
    font-size: 1.1rem;
  }

  &--full-width {
    width: 100%;
  }

  &__icon {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__hover-effect {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(
      45deg,
      transparent,
      rgba(white, 0.2),
      transparent
    );
    transform: translateX(-100%);
    transition: transform 0.6s ease;
  }

  &:hover &__hover-effect {
    transform: translateX(100%);
  }
}

@media (max-width: $breakpoint-mobile) {
  .button {
    &--medium {
      padding: 0.7rem 1.5rem;
      font-size: 0.95rem;
    }

    &--large {
      padding: 0.9rem 2rem;
      font-size: 1rem;
    }
  }
}
